.create-btn {
    display: block;
    margin: auto;
}

.create-window,
.login-window {
    position: relative;
    z-index: 9999;
    background: white;
    width: 95%;
    max-width: 600px;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, .30);
    margin: auto;
    margin-bottom: 140px;
}

h1 {
    text-align: center;
    position: relative;
    z-index: 9999;
    padding-top: 120px;
    margin: 0px 0px 100px;
    color: #7abb54;
    text-shadow: 0px 2px 3px rgba(0, 0, 0, .20);
    font-weight: 500;
    font-size: 42px;
    animation-name: fade-in;
    animation-duration: 1s;
}

@keyframes fade-in {
    0%      {opacity: 0; top: 100px;}
    100%    {opacity: 1; top: 0px;}
}

h2 {
    text-align: center;
    margin: 0px;
    margin-bottom: 40px;
    color: #888;
    font-weight: 400;
    font-size: 28px;
}

h6 {
    text-align: center;
    margin: 10px 0px;
    font-weight: 500;
    color: #aaa;
    font-size: 14px;
}

.login-submit, .create-btn {
    margin: auto;
    width: 92%;
    max-width: 450px;
    height: 35px;
    border-radius: 6px;
    border: none;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, .30);
    font-size: 18px;
    color: white;
    background: #7abb54;
}

.login-submit:hover,
.create-btn:hover {
    cursor: pointer;
    background: #6da84b;
}

input {
    margin: auto;
    margin-bottom: 20px;
    width: 90%;
    max-width: 442px;
    border-radius: 6px;
    border: 1px solid #aaa;
    padding: 6px 4px;
}

label {
    margin: auto;
    text-align: left;
    width: 92%;
    max-width: 450px;
    font-size: 14px;
    color: #aaa;
}

textarea {
    margin: auto;
    width: 92%;
    max-width: 438px;
    border-radius: 10px;
    border: none;
    height: 60px;
    margin-bottom: 20px;
    resize: none;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.16) inset;
    background: #fdfdfd;
    font-family: "Segoe UI",Arial,sans-serif;
    padding: 6px 8px;
}

.login-btn,
.create-btn {
    margin-top: 40px;
}

.create-window *,
.login-window * {
    display: block;
}

.create-window,
.login-window {
    padding: 60px 0px;
    animation-name: fade-in;
    animation-duration: 1s;
}

.hidden {
    display: none;
}

.login-here {
    color: #7abb54;
}

.login-here:hover {
    cursor: pointer;
    color: #6da84b;
}

.video-cover-top {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 230px;
    background: #f0f0f0;
}

.video-cover-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 300px;
    background: #88B565;
    max-height: 50%;
}

.dashboard-cover-top {
    height: 0px;
    transition: 2s;
}

.dashboard-cover-bottom {
    height: 600px;
    transition: 2s;
}

.login-video {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

.dashboard-video {
    position: fixed;
    top: -250px;
    left: 0;
    z-index: -1;
    transition: 1s;
}

.background-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    background: white;
    opacity: 60%;
}

.has-acc-msg {
    position: relative;
    margin: 50px auto 0px;
}

.has-acc-msg h6 {
    text-align: center;
    display: inline-block;
}

.login-here {
    margin: 0px 5px;
}