.dashboard-container {
    position: fixed;
    z-index: 5000;
    animation-name: dashboard-fade-in;
    animation-duration: 2s;
    width: 100%;
}

@keyframes dashboard-fade-in {
    0%      {opacity: 0}
    50%     {opacity: 0}
    100%    {opacity: 1}
}

.dashboard-container svg {
    fill: black;
    opacity: 0.1;
}

.dashboard-container svg:hover {
    cursor: pointer;
    opacity: 0.3;
}

.active svg {
    opacity: 0.3;
}

.active svg:hover {
    cursor: default;
}

.house {
    width: 60%;
    max-width: 150px;
    margin: 40px auto 0px;
}

.step-6 {
    width: 35%;
    max-width: 100px;
    margin: auto;
}

.step-5 {
    width: 55%;
    max-width: 140px;
    margin: auto;
}

.step-4 {
    width: 75%;
    max-width: 195px;
    margin: auto;
}

.step-3 {
    width: 100%;
    max-width: 270px;
    margin: auto;
}

.step-2 {
    width: 380px;
    margin: auto;
}

.step-1 {
    width: 530px;
    margin: auto;
}

.step-0 {
    width: 740px;
    margin: auto;
}

.info-window {
    position: relative;
    text-align: left;
    top: 600px;
    z-index: 9999;
    background: white;
    width: 90%;
    max-width: 800px;
    margin: 0px auto;
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
    animation-name: info-fade-in;
    animation-duration: 3s;
}

@keyframes info-fade-in {
    0%      {top: 1000px}
    50%     {top: 1000px}
    100%    {top: 600px}
}

.info-window h2 {
    text-align: left;
    padding: 20px 20px 0px;
    margin: 0px;
}

.info-window p {
    text-align: left;
    padding: 20px 20px;
    margin: 0px;
}