.link-btn {
    opacity: 0.6;
    width: 100%;
    height: 300px;
    margin: 1vh auto;
    border: none;
    background: rgba(0, 0, 0, 0.2);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    transition: 200ms;
}

.link-btn:hover {
    cursor: pointer;
    transition: 200ms;
    opacity: 1.0;
}

.link-btn h3 {
    position: relative;
    z-index: 9999;
    text-shadow: 2px 2px 4px black;
    font-size: 32px;
    color: white;
}

@media (min-width: 1000px) {
    .link-btn {
        width: 24.5vw;
        height: 99vh;
        margin: .5vh .25vw;
        border: none;
        background: rgba(0, 0, 0, 0.2);
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        transition: 200ms;
    }

    .link-btn h3 {
        font-size: 26px;
    }
}

@media(min-width: 1200px) {
    .link-btn h3 {
        font-size: 30px;
    }
}

@media(min-width: 1400px) {
    .link-btn h3 {
        font-size: 36px;
    }
}




